// src/app/levels/[category]/page.tsx
import LevelSelector from '@/components/LevelSelector';
import { Metadata } from 'next';
import { notFound } from 'next/navigation';
import levelsData from '@/data/levels.json';

interface Level {
  id: number;
  category: string;
  title: string;
  videoId: string;
  thumb: string;
}

export async function generateMetadata({ params }: { 
  params: { category: string } 
}): Promise<Metadata> {
  const categoryName = params.category === 'normal' 
    ? 'Normal Levels' 
    : params.category.charAt(0).toUpperCase() + params.category.slice(1);
  
  return {
    title: `${categoryName} - Perfect Tidy Level Guides`,
    description: `Complete walkthroughs and solutions for ${categoryName} levels in Perfect Tidy game.`,
    alternates: {
      canonical: `https://www.perfecttidyhub.com/levels/${params.category}`,
    },
  };
}

export async function generateStaticParams() {
  // 获取所有唯一的分类
  const categories = Array.from(new Set(levelsData.map(level => level.category)));
  
  return categories.map(category => ({
    category,
  }));
}

export default function CategoryLevelsPage({ params }: { 
  params: { category: string } 
}) {
  // 过滤出该分类下的所有关卡
  const categoryLevels = (levelsData as Level[]).filter(level => 
    level.category === params.category
  );
  
  if (categoryLevels.length === 0) {
    notFound();
  }
  
  const categoryName = params.category === 'normal' 
    ? 'Normal Levels' 
    : params.category.charAt(0).toUpperCase() + params.category.slice(1);
  
  return (
    <div className="min-h-screen bg-[#f0f4f8] p-6">
      <div className="max-w-6xl mx-auto">
        <div className="text-center mb-10">
          <h1 className="text-4xl font-bold text-blue-600 mb-4">
            {categoryName} - Perfect Tidy Level Guides
          </h1>
          <p className="text-xl text-amber-700">
            Complete walkthroughs and solutions for all {categoryName} levels.
          </p>
        </div>
        
        <LevelSelector 
          initialCategory={categoryName} 
          initialLevels={categoryLevels} 
        />
      </div>
    </div>
  );
}